import "./AboutContentStyles.css";

import React from "react";
import { Link } from "react-router-dom";
import JAVASCRIPT from "../assets/js.png"
import JAVA from "../assets/java.png"
import SPRINGBOOT from "../assets/SpringBoot.png"
const AboutContent = () => {
  return (
    <div className="about">
      <div className="left">
        <h1>Who Am I?</h1>
        <p>저는 Spring Cloud MSA 기반의 백엔드 개발자로,<br/>
        "문제의 답은 공식 문서에 있다"를 신조로 삼고 있습니다.<br/>
        이전 프로젝트에서 어려운 문제에 직면할 때,<br/>
        공식 문서를 참고하여 성공적으로 해결한 경험이 있습니다.<br/>
        이러한 노하우를 토대로 Spring Cloud와 MSA에 대한 전문 지식을<br/>
        </p>
        <Link to="/contact">
          <button className="btn">Contact</button>
        </Link>
      </div>
      <div className="right">
        <h1>Skills</h1>
        <div className="img-container">
          <div className="img-stack">
            <img src={JAVA}
            className="img" 
            alt="true" />
          </div>
          <div className="img-stack">
            <img src={JAVA}
            className="img" 
            alt="true" />
          </div>
          <div className="img-stack">
            <img src={JAVASCRIPT} 
            className="img" 
            alt="true" />
          </div>
          <div className="img-stack">
            <img src={JAVA}
            className="img" 
            alt="true" />
          </div>
          <div className="img-stack">
            <img src={JAVA}
            className="img" 
            alt="true" />
          </div>
          <div className="img-stack">
            <img src={SPRINGBOOT}
                 className="img"
                 alt="true" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutContent;
